.row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 5px;
}

.row > button {
  margin-left: 4px;
  margin-right: 8px;
}
.row:not(:last-child) {
  margin-bottom: 16px;
}

.value {
  font-size: 78px;
  padding-left: 16px;
  padding-right: 16px;
  margin-top: 2px;
  font-family: 'Courier New', Courier, monospace;
}

.letter {
  appearance: none;
  background: none;
  font-size: 32px;
  padding-left: 12px;
  padding-right: 12px;
  outline: none;
  border: 2px solid transparent;
  color: rgb(112, 76, 182);
  padding-bottom: 4px;
  cursor: pointer;
  background-color: rgba(112, 76, 182, 0.1);
  border-radius: 2px;
  transition: all 0.15s;
}

.letter:hover,
.letter:focus {
  border: 2px solid rgba(112, 76, 182, 0.4);
}

.letter:active {
  transform: scale(0.98);
  border: 2px solid rgba(112, 76, 182, 0.2);
}

.thinger {
  overflow-y: hidden;
}

.icon {
  height: 256px;
}

.icon > img {
  height: 100%;
}

.letter {
  font-size: 200%;
  padding: 1em;
}
